import React, { useCallback } from 'react';
import { Bottom } from '../../components/Buttons';
import { FaTimes, FaWindowMaximize, FaCogs } from 'react-icons/fa';
import styles from '../../css/profile.module.css';

export default function Banner(props: {
	displayPlayer: string
	clearDisplayPlayer: () => void
	toggleFullScreen: () => void
}) {

	return (
		<div className={['bottomBannerBackground', styles.bottomBanner].join(' ')}>
			<div className='bottomBanner'>
				{Bottom(
					!props.displayPlayer ? null : [
						props.clearDisplayPlayer,
						<FaTimes key='close' />,
						'Close profile'
					],
					[
						props.toggleFullScreen,
						<FaCogs key='fullscreen' />,
						'Detailed settings view'
					]
				)}
			</div>
		</div>
	)
}